@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
:root {
  --title-font: 'Pacifico', cursive;
  --body-font: 'Roboto', sans-serif;
}

body, html {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

*,*:before,*:after {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-family: var(--body-font);
}

body, .btn, input, select, options, textarea, a, p {
  font-size: 1.8rem!important;
}


/* Global styles and variables */
:root {
    /* COLORES */
    --primary-color: #1C5D99;
    --secondary-color: #BBCDE5;
    --accent: #EBEBEB;
    --accent2: #267DCF;
    --white: #ffffff;
    --black: #222222;
    /* Radio de los bordes */
    --borderRadius: .5rem;
}
.no-text-dec {
    text-decoration: none;
}
.btn {
    display: block!important;
    width: 100%;
    border: none!important;
    border-radius: 0.25rem!important;
}
.btn-primary {
    background-color: #1C5D99!important;
    background-color: var(--primary-color)!important;
    color: #ffffff!important;
    color: var(--white)!important;
    border: 2px solid #1C5D99!important;
    border: 2px solid var(--primary-color)!important;
}
.btn-primary:hover {
    background-color: #BBCDE5!important;
    background-color: var(--secondary-color)!important;
    color: #222222!important;
    color: var(--black)!important;
    border-color: #BBCDE5!important;
    border-color: var(--secondary-color)!important;
}
.btn-secondary {
    background-color: transparent!important;
    border: 2px solid #1C5D99!important;
    border: 2px solid var(--primary-color)!important;
    color: #1C5D99!important;
    color: var(--primary-color)!important;
    font-weight: 700;
}
.btn-secondary:hover {
    box-shadow: inset 0 0 0 2px #1C5D99;
    box-shadow: inset 0 0 0 2px var(--primary-color);
}
.btn-item {
    background-color: #ffffff !important;
    background-color: var(--white) !important;
    border: none !important;
    color: #222222 !important;
    color: var(--black) !important;
}
.center-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
}
.container {
    width: 95%!important;
    max-width: 120rem!important;
    margin: 0 auto!important;
}
.container-fluid {
    width: 95%!important;
}
@media only screen and (min-width: 768px) {
    .btn {
        display: inline-block!important;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
}   
.navbar.navbar-light {
    background-color: #ffffff!important;
    box-shadow: 0 2px 4px -2px rgba(0, 0, 0, .4);
}
.navbar-brand img {
    max-width: 8rem;
}

.site-name {
    font-family: var(--title-font);
    font-size: 3rem;
    padding: 1rem;
    margin-right: 2rem;
}
.carrito {
    align-items: center;
    grid-gap: 3px;
    gap: 3px;
}

.carrito img {
    min-width: 2.7rem;
}

.carrito .badge-carrito {
    text-decoration: none!important;
}
.item {
    background-color: var(--accent);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: var(--borderRadius);
    overflow: hidden;
}

.item__image-container img{
    max-width: 100%;
}
.item__info {
    padding: 1rem;
}

.item__options {
    display: flex;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
}
.items-container {
    display: grid;
    margin-top: 5rem;
    grid-gap: 1rem;
    gap: 1rem;
    grid-template-columns: 1fr;
}

@media only screen and (min-width: 640px) {
    .items-container {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (min-width: 992px) {
    .items-container {
        grid-template-columns: 1fr 1fr 1fr;
    }
}
.footer {
    margin-top: 5rem;
    text-align: center;
    padding: 2rem;
    background-color: var(--black);
}
.footer .copyright {
    color: var(--white);
    margin: 0;
}

.footer .copyright a {
    color: var(--secondary-color);
    text-decoration: none;
}
.footer .copyright a:hover {
    text-decoration: underline;
}

.item-detail {
    background-color: var(--accent);
    margin: 5rem auto;
    border-radius: var(--borderRadius);
    overflow: hidden;
}

.item-detail__carousel .slide__image {
    width: 100%;
}
.item-detail__info {
    padding: 2rem;
}
.item-detail__info .item-detail__title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
}
.item-detail__info .item-detail__price {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
}

.cargando-msg {
    padding: 2rem;
    font-weight: 700;
    font-size: 1.5rem;
    color: var(--black);
    margin: 0;
}

@media only screen and (min-width: 768px) {
    .item-detail__carousel, .item-detail {
        max-width: 75%;
        margin: 0 auto;
    }
    .item-detail {
        margin-top: 5rem;
    }
}

.item-counter-container {
    position: relative;
    display: flex;
    grid-gap: .5rem;
    gap: .5rem;
    align-items: center;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
}

.item-counter {
    display: flex;
    align-items: center;
    width: 10rem;
}

.item-counter p {
    margin: 0;
    background-color: #dadada;
    padding: .49rem 1rem;
    height: 100%;
}

.item-counter button {
    width: 3rem;
}

.delete-icon,
.add-icon {
    border: none;
    background: transparent;
}

.item-counter-container .stock-error {
    position: absolute;
    left: 0;
    background-color: #dadada;
    text-align: center;
    top: 0;
    min-width: 100%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    opacity: .9;
    font-weight: 700;
    padding: .5rem;
}

.item-counter-container .add-icon {
    min-width: 3.6rem;
    padding: 0;
}

.item-counter-container .add-icon[disabled] {
    opacity: .5;
}

.item-counter-container .added-alert {
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 25%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 5rem;
    background-color: var(--primary-color);
    box-shadow: 0 2px 8px -2px rgba(0, 0, 0, .6);
    color: var(--white);
    z-index: 999;
}

.modal-button {
    visibility: hidden;
}
.modal-footer {
    border: none;
}
h1 {
    text-align: center;
    margin-top: 5rem;
}

.cart-empty {
    text-align: center;
    margin-top: 5rem;
}

.cart-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    background: var(--accent);
    padding: .5rem;
    margin-bottom: 2rem;
}
.cart-item-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}
.cart-item-img img{
    max-width: 100%; 
}
.cart-item-img {
    width: 20rem;
}
.cart-item-info {
    width: 100%;
}

.cart-item-name,
.cart-item-total {
    font-weight: bold;
}
.cart-opciones {
    margin: 2rem 0;
    display: flex;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
}
.cart-total {
    width: 100%;
    text-align: center;
    margin: 2rem 0 2rem auto;
    border-radius: 0.2rem;
    border: 2px solid var(--primary-color);
    padding: 1rem;
}
.cart-total p {
    font-weight: 700;
    font-size: 2.2rem!important;
    margin: 0;
}

@media only screen and (min-width: 768px) {
    .cart-item {
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    .cart-item-content {
        flex-direction: row;
    }
    .cart-item-info {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
    .cart-total {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        text-align: start;
    }
}
.checkout-container {
    margin: 5rem auto 0 auto;
    padding: 2rem;
    box-shadow: 0 3px 10px -4px rgba(0, 0, 0, 0.3);
}

.checkout-container .checkout-title {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 2rem;
}

.checkout-container .field {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    max-width: 42rem;
}

.checkout-btn {
    margin-top: 2rem;
}

.checkout-options button {
    margin-bottom: 2rem;
}
.checkout-options button:last-child {
    margin-bottom: 0;
}

@media only screen and (min-width: 768px) {
    .checkout-container {
        width: 70%;
    }
    .checkout-container .checkout-title {
        font-size: 2.4rem;
        text-align: start;
    }
    .checkout-options {
        display: flex;
        justify-content: space-between;
        grid-gap: 2rem;
        gap: 2rem;
    }
    .checkout-options button {
        margin: 0;
    }
}
.checkout-final-step h1 {
    font-size: 2.4rem!important;
}
.checkout-final-step h2, .checkout-final-step h1 {
    font-weight: 700!important;
    margin-bottom: 2rem;
}
.datos-flex {
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 5rem 0;
}
.checkout-total {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 2rem;
}
@media only screen and (min-width: 768px) {
    .datos-flex {
        flex-direction: row;
        justify-content: space-evenly;
    }
    
}
.to-top-arrow {
    background-color: var(--primary-color);
    border-radius: 50%;
    position: fixed;
    width: 4.2rem;
    height: 4.2rem;
    bottom: 5rem;
    right: 5rem;
    box-shadow: 0 2px 4px 0px rgba(0, 0, 0, .7);
    cursor: pointer;
    z-index: 9999;
}

.to-top-arrow .arrow-icon {
    position: absolute;
    width: 2rem;
    height: 2rem;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
}
